"use client";

import useWindowDimension from "@/hooks/useWindowDimension";
import { useState, useEffect } from "react";
import NativeImage from "../ui/NativeImage";

const Navbar = () => {
  const { scrollY } = useWindowDimension();
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    if (scrollY > 50) {
      setIsScrolled(true);
    } else {
      setIsScrolled(false);
    }
  }, [scrollY]);

  return (
    <nav
      className={`z-10 block w-full fixed top-0 transform translate-z-0 transition-all duration-300 max-h-[57px] ${
        isScrolled
          ? "bg-white backdrop-blur-lg backdrop-brightness-[1.2]  -webkit-backdrop-filter: blur(8px) brightness(1.2)"
          : ""
      }`}
    >
      <div className="wrap px-[36px] max-w-[1920px] mx-auto bg-transparent">
        <div
          className={`flex justify-between ${
            isScrolled ? "items-center" : "items-center"
          }  w-full px-0 py-[12px]`}
        >
          <div className="left flex items-center space-x-4">
            <span>
              <div className="flex flex-col justify-between items-center w-5 h-5 bg-transparent border-0">
                <span className="block w-full h-[3px] bg-black"></span>
                <span className="block w-full h-[3px] bg-black"></span>
                <span className="block w-full h-[3px] bg-black"></span>
              </div>
            </span>
            <span className="font-regular-title-bold">MENU</span>
          </div>

          <div className="mid text-center flex-1 relative">
            {isScrolled ? (
              <NativeImage
                url="https://vtlogo.com/wp-content/uploads/2020/12/lofficiel-vector-logo-small.png"
                width={1200}
                height={600}
              />
            ) : (
              <NativeImage
                url="https://vtlogo.com/wp-content/uploads/2020/12/lofficiel-vector-logo-small.png"
                width={1200}
                height={600}
              />
            )}
          </div>

          <div className="right flex items-center space-x-4 font-regular-title-bold">
            <span>BUY THE ISSUE</span>
            <span>USA</span>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
